import { LayoutName, useStore } from '@/shared/lib/index.js';

interface BackgroundUrls {
    backgroundFirstPhaseUrl: string;
    backgroundSecondPhaseUrl: string;
    backgroundPlaceholder: string;
}

export const useBackgroundUrls = (): BackgroundUrls => {
    const { settings } = useStore();

    // eslint-disable-next-line @typescript-eslint/no-var-requires
    let backgroundFirstPhaseUrl: string = require('../ui/assets/background_phase_1.webp').default.src;

    // eslint-disable-next-line @typescript-eslint/no-var-requires
    let backgroundSecondPhaseUrl = require('../ui/assets/background_phase_2.webp').default.src;

    // eslint-disable-next-line @typescript-eslint/no-var-requires
    let backgroundPlaceholder = require('../ui/assets/background_placeholder.webp').default.src;

    if (settings.layout === LayoutName.Mobile) {
        // eslint-disable-next-line @typescript-eslint/no-var-requires
        const backgroundMobile = require('../ui/assets/background_mobile.webp').default.src;

        backgroundFirstPhaseUrl = backgroundMobile;
        backgroundSecondPhaseUrl = backgroundMobile;
        backgroundPlaceholder = backgroundMobile;
    }

    return {
        backgroundFirstPhaseUrl,
        backgroundSecondPhaseUrl,
        backgroundPlaceholder
    };
};
